.featured {
  height: 100vh;
  font-size: 18px;
  color: #999;
}

.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}

.featured--name {
  color: #fff;
  font-size: 60px;
  font-weight: bold;
}

.featured--info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.featured--points,
.featured--year,
.featured--seasons {
  color: #fff;
  display: inline-block;
  margin-right: 15px;
}

.featured--points {
  color: #46d369;
}

.featured--description {
  margin-top: 15px;
  max-width: 40%;
  font-size: 20px;
  font-weight: 400;
  color: #999;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
}

.featured--buttons {
  margin-top: 25px;
}
.featured--watchbutton,
.featured--mylistbutton {
  display: inline-block;
  font-size: 20px;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  opacity: 1;
  transition: all ease 0.2s;
}
.featured--watchbutton {
  background-color: #fff;
  color: #111;
}

.featured--mylistbutton {
  background-color: #333;
  color: #fff;
}
.featured--genres {
  padding: 20px 0;
}

.featured--watchbutton:hover,
.featured--mylistbutton:hover {
  opacity: 0.75;
}

@media (max-width: 760px) {
  .featured {
    height: 80vh;
  }
  .featured--name {
    font-size: 36px;
    margin-right: 30px;
  }
  .featured--info {
    font-size: 16px;
  }
  .featured--description {
    font-size: 14px;
    max-width: 100%;
    margin-right: 30px;
  }
  .featured--watchbutton,
  .featured--mylistbutton {
    font-size: 16px;
    padding: 10px 25px;
  }
  .featured--genres {
    font-size: 14px;
    padding: 15px;
    margin-right: 30px;
  }
}
